* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav{
  width: 100%;
  height: 100px;

}

.container {
   width: 1180px;
   margin: 0 auto;
}

nav ul {
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(8,1fr);
}

nav ul li {
  font-size: 20px;
  padding-top: 10px;
  list-style: none;
}

button {
  background: #ff7a50;
  color: #fff;
  width: 130px;
  height: 50px;
  border: 1px solid #Fa7a50;
  border-radius: 5px;  
}

.too {
  display: flex;
  gap: 30px;
}

.met {
  padding: 20px;
  text-decoration: underline;
}

.otadiv {
  display: grid;
  grid-template-columns: repeat(2,1fr);
}

.to {
  padding: 30px;
  text-align: center;
} 

.ota {
  display: grid;
  grid-template-columns: repeat(5,1fr);
  gap: 10px;
}
 
.m {
  background: #fff;
  border: 1px solid #fff;
  color: #363853;
}

.bola {
  display: grid;
  grid-template-columns: repeat(5,1fr);
  gap: 10px;
}

.section__otadiv__button {
  gap: 20px;
  display: grid;
  grid-template-columns: repeat(5,1fr);
}

.section__otadiv1 {
  gap: 20px;
  display: grid;
  grid-template-columns: repeat(5,1fr);
}

.section__otadiv__card__btn__active {
  background: #FF7A50;
  color: aliceblue;
  border: none;
  height: 55px;
  width: 240px;
  border-radius: 5px;
}

.section__otadiv__card__btn {
  border: none;
  background: #ffffff;
  border-radius: 5px;
  width: 240px;
  height: 55px;
}

.section__otadiv__card__img {
  width: 20%;
}

.section__otadiv1__card {
  padding: 20px;
  border-radius: 10px;
  background: #ffffff;
}

.section__otadiv1__card__text {
  background: #ffffff;
}

.section__otadiv1__card__btn2 {
  background: #FF7A50;
  color: aliceblue;
  height: 40px;
  width: 200px;
  border: none;
  border-radius: 5px;
}

.section__otadiv1__card__img {
  width: 90%;
}



